.radioButtonIcon {
  position: relative;
  width: 32px;
  height: 32px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 0;
}
.text,
.text1,
.text2 {
  position: relative;
  line-height: 120%;
  font-weight: 600;
}
.text1 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 27px;
  height: 16px;
  flex-shrink: 0;
}
.text2 {
  font-size: 16px;
}
.topText {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 8px;
}
.text3,
.text4 {
  position: relative;
  line-height: 120%;
}
.text4 {
  font-size: 10px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 19px;
  height: 12px;
  flex-shrink: 0;
}
.bottomText,
.rightTextContainer {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.bottomText {
  flex-direction: row;
  gap: 8px;
}
.rightTextContainer {
  flex-direction: column;
  font-size: 12px;
}
.textContainer {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}
.offerExpired {
  position: absolute;
  top: 1px;
  left: 29px;
  line-height: 120%;
  font-weight: 500;
}
.tag {
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 62px;
  border-radius: 0 0 4px 4px;
  background-color: #f77171;
  width: 121px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 2;
  font-size: 12px;
  color: #fff;
}
.textField1 {
  align-self: stretch;
  border-radius: 4px;
  background-color: #e7e7e7;
  border: 2px solid #bebebe;
  display: flex;
  flex-direction: row;
  padding: 16px;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  color: #bebebe;
}
.radioButton,
.text6,
.text9,
.textField1 {
  position: relative;
}
.text6,
.text9 {
  line-height: 120%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 27px;
  height: 16px;
  flex-shrink: 0;
}
.text9 {
  font-size: 10px;
  font-weight: 500;
  color: #bebebe;
  width: 19px;
  height: 12px;
}
.leftTextContainer,
.textContainer1 {
  display: flex;
  justify-content: flex-start;
}
.leftTextContainer {
  flex-direction: column;
  align-items: flex-end;
  font-size: 12px;
}
.textContainer1 {
  flex-direction: row;
  align-items: center;
}
.recommended {
  position: absolute;
  top: 1px;
  left: 25px;
  font-size: 12px;
  line-height: 120%;
  font-weight: 500;
  font-family: Roboto;
  color: #fff;
  text-align: center;
}
.tag1 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: #47ba68;
  position: relative;
  border-radius: 0 0 4px 4px;
  width: 121px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.textField2 {
  position: absolute;
  top: -1px;
  left: -1px;
  border-radius: 4px;
  border: 2px solid #bebebe;
  box-sizing: border-box;
  width: 503px;
  display: flex;
  flex-direction: row;
  padding: 16px;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
}
.textfield2 {
  position: relative;
  width: 501px;
  height: 65px;
}
.inputContainer {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  text-align: center;
  font-size: 16px;
  color: #3c4852;
  font-family: Roboto;
}
