.radioButton {
  position: relative;
}
.text {
  font-weight: 600;
}
.text,
.text1,
.text2 {
  position: relative;
  line-height: 120%;
}
.text1 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 27px;
  height: 16px;
  flex-shrink: 0;
}
.text2 {
  font-size: 16px;
  font-weight: 600;
}
.topText {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 8px;
}
.text3,
.text4 {
  position: relative;
  line-height: 120%;
}
.text4 {
  font-size: 10px;
  font-weight: 500;
  color: #bebebe;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 19px;
  height: 12px;
  flex-shrink: 0;
}
.bottomText,
.leftTextContainer {
  display: flex;
  align-items: flex-end;
}
.bottomText {
  flex-direction: row;
  justify-content: center;
  gap: 8px;
}
.leftTextContainer {
  flex-direction: column;
  justify-content: flex-start;
  font-size: 12px;
}
.textContainer,
.textField3 {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.textContainer {
  flex: 1;
  justify-content: space-between;
}
.textField3 {
  align-self: stretch;
  border-radius: 4px;
  border: 2px solid #bebebe;
  padding: 16px;
  justify-content: flex-start;
  gap: 12px;
  text-align: center;
  font-size: 16px;
  color: #3c4852;
  font-family: Roboto;
}
