.edyoda {
  position: absolute;
  top: 0;
  left: 0;
  letter-spacing: 0.03em;
  font-weight: 900;
  background: linear-gradient(90.72deg, #0096ff 42.14%, #0048ff 96.7%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.logo,
.text {
  position: relative;
}
.logo {
  width: 158px;
  height: 45px;
  font-size: 38px;
}
.text {
  font-weight: 500;
}
.iconChevronDown {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.courses,
.leftContent {
  display: flex;
  align-items: center;
}
.courses {
  flex-direction: row;
  justify-content: flex-start;
  gap: 12px;
}
.leftContent {
  gap: 64px;
}
.leftContent,
.logIn {
  flex-direction: row;
  justify-content: center;
}
.container,
.logIn,
.navBar {
  display: flex;
  align-items: center;
}
.container {
  flex-direction: row;
  justify-content: flex-start;
  gap: 851px;
}
.navBar {
  background-color: #fff;
  width: 1920px;
  height: 72px;
  flex-direction: column;
  padding: 24px 80px;
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
  font-size: 16px;
  color: #000;
  font-family: Raleway;
}
